import { useState, useRef, useEffect } from "react";
import { useNavigate, Link } from "react-router-dom";
import axios from "axios";
import ReCAPTCHA from "react-google-recaptcha";
import API_BASE_URL from "../config";
import "./Auth.css";

console.log('🔧 Environment variables:', {
  NODE_ENV: process.env.NODE_ENV,
  REACT_APP_API_BASE_URL: process.env.REACT_APP_API_BASE_URL,
  API_BASE_URL
});

const Auth = () => {
  const [isRegistering, setIsRegistering] = useState(false);
  const [email, setEmail] = useState("");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [message, setMessage] = useState("");
  const [recaptchaToken, setRecaptchaToken] = useState(null);
  const [isAccountInactive, setIsAccountInactive] = useState(false);
  const [userEmail, setUserEmail] = useState("");
  const navigate = useNavigate();
  const recaptchaRef = useRef(null);

  const isDevelopment = process.env.NODE_ENV === 'development';
  console.log('🔧 Development mode:', isDevelopment);
  console.log('🔧 NODE_ENV:', process.env.NODE_ENV);

  // Добавляем Eruda всегда для отладки на мобильных устройствах
  useEffect(() => {
    const script = document.createElement('script');
    script.src = "//cdn.jsdelivr.net/npm/eruda";
    script.onload = () => {
      window.eruda.init();
      console.log('📱 Eruda initialized for mobile debugging');
    };
    document.body.appendChild(script);
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setMessage("");
    setIsAccountInactive(false);

    console.log('📝 Form submission:', {
      isRegistering,
      username,
      isDevelopment,
      API_BASE_URL
    });

    if (!isDevelopment && !recaptchaToken) {
      setMessage("❌ Подтвердите, что вы не робот");
      return;
    }

    if (isRegistering) {
      if (password !== confirmPassword) {
        setMessage("❌ Пароли не совпадают");
        return;
      }
      if (password.length < 8) {
        setMessage("❌ Пароль должен быть не менее 8 символов");
        return;
      }

      try {
        await axios.post(`${API_BASE_URL}/api/register/`, {
          username,
          email,
          password,
          confirm_password: confirmPassword,
          recaptcha: isDevelopment ? 'development_mode' : recaptchaToken,
        });

        setMessage("✅ Регистрация успешна! Перенаправление...");
        setTimeout(() => navigate("/auth"), 2000);
      } catch (error) {
        console.error("❌ Ошибка регистрации:", error);
        setMessage(
          error.response?.data?.error || "❌ Ошибка регистрации. Проверьте данные."
        );
      }
    } else {
      try {
        console.log('🔐 Отправка запроса на вход:', {
          username,
          recaptcha: isDevelopment ? 'development_mode' : recaptchaToken
        });
        
        const response = await axios.post(`${API_BASE_URL}/api/login/`, {
          username,
          password,
          recaptcha: isDevelopment ? 'development_mode' : recaptchaToken,
        });

        console.log('✅ Успешный ответ:', response.data);
        localStorage.setItem("token", response.data.token);
        navigate("/dashboard");
      } catch (error) {
        console.error("❌ Ошибка входа:", error.response || error);
        if (error.response?.status === 403 && error.response?.data?.resend) {
          setMessage("❌ Аккаунт не активирован. Пожалуйста, активируйте ваш аккаунт.");
          setIsAccountInactive(true);
          setUserEmail(error.response.data.email);
        } else {
          setMessage(
            error.response?.data?.non_field_errors?.[0] || "❌ Неверный логин или пароль"
          );
        }
        if (!isDevelopment) {
          setRecaptchaToken(null);
          recaptchaRef.current?.reset();
        }
      }
    }
  };

  const handleResendActivation = async () => {
    try {
      const response = await axios.post(`${API_BASE_URL}/api/resend-activation/`, {
        email: userEmail,
      });
      if (response.status === 200) {
        setMessage("✅ Письмо с активацией отправлено повторно.");
      } else {
        setMessage("❌ Не удалось отправить письмо повторно.");
      }

      setMessage("✅ Письмо с активацией отправлено повторно.");
      setIsAccountInactive(false);
    } catch (error) {
      console.error("❌ Ошибка при повторной отправке письма активации:", error);
      setMessage(
        error.response?.data?.error || "❌ Ошибка при повторной отправке письма."
      );
    }
  };

  return (
    <div className="auth-container">
      <div className="auth-card">
        <h2>{isRegistering ? "Регистрация" : "Вход"}</h2>
        {message && <p className="error-message">{message}</p>}
        <form onSubmit={handleSubmit}>
          <input
            type="text"
            placeholder="Логин"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            required
          />

          {isRegistering && (
            <input
              type="email"
              placeholder="Email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
            />
          )}

          <input
            type="password"
            placeholder="Пароль"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />

          {isRegistering && (
            <input
              type="password"
              placeholder="Подтвердите пароль"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              required
            />
          )}

          {!isDevelopment && (
            <ReCAPTCHA
              ref={recaptchaRef}
              sitekey={process.env.REACT_APP_RECAPTCHA_SITE_KEY}
              onChange={(token) => setRecaptchaToken(token)}
              theme="light"
            />
          )}

          <button type="submit" className="auth-button">
            {isRegistering ? "Зарегистрироваться" : "Войти"}
          </button>
        </form>
        {!isRegistering && (
          <p className="switch-button">
            <Link to="/password-reset">Забыли пароль?</Link>
          </p>
        )}

        {isAccountInactive && (
          <button onClick={handleResendActivation} className="switch-button">
            Отправить письмо повторно
          </button>
        )}

        <button
          onClick={() => setIsRegistering(!isRegistering)}
          className="switch-button"
        >
          {isRegistering ? "Уже есть аккаунт? Войти" : "Зарегистрироваться"}
        </button>
      </div>
    </div>
  );
};

export default Auth;
