import React, { useState, useEffect, useCallback, forwardRef, useImperativeHandle } from 'react';
import axios from 'axios';
import * as XLSX from 'xlsx';
import './FurnitureList.css';
import API_BASE_URL from '../config';

const FurnitureList = forwardRef(({ orderNumber, fileUrl }, ref) => {
  const [furnitureItems, setFurnitureItems] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [saving, setSaving] = useState(false);

  // Загрузка и парсинг Excel файла
  const loadFurnitureFile = useCallback(async () => {
    try {
      setLoading(true);
      const response = await axios.get(`${fileUrl}?t=${Date.now()}`, {
        responseType: 'arraybuffer',
        headers: {
          'Authorization': `Token ${localStorage.getItem('token')}`
        }
      });

      const data = new Uint8Array(response.data);
      const workbook = XLSX.read(data, { type: 'array' });
      const firstSheetName = workbook.SheetNames[0];
      const worksheet = workbook.Sheets[firstSheetName];
      const jsonData = XLSX.utils.sheet_to_json(worksheet);

      const items = jsonData.map((row, index) => ({
        id: index,
        name: row['Наименование'] || row['название'] || row[Object.keys(row)[0]],
        quantity: row['Количество'] || row['количество'] || row[Object.keys(row)[1]],
        purchased: (row['Куплено'] || row['куплено'])?.toString().trim().toLowerCase() === 'да'
      }));

      setFurnitureItems(items);
      setError(null);
    } catch (err) {
      console.error('Ошибка загрузки файла:', err);
      setError('Не удалось загрузить список фурнитуры');
    } finally {
      setLoading(false);
    }
  }, [fileUrl]);

  // Сохранение Excel файла
  const saveUpdatedFile = async (itemsToSave) => {
    try {
      setSaving(true);
      const worksheet = XLSX.utils.json_to_sheet(itemsToSave.map(item => ({
        'Наименование': item.name,
        'Количество': item.quantity,
        'Куплено': item.purchased ? 'Да' : 'Нет'
      })));
      const workbook = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(workbook, worksheet, 'Фурнитура');

      const excelBuffer = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
      const blob = new Blob([excelBuffer], {
        type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
      });

      const formData = new FormData();
      formData.append('file', blob, 'furniture_list.xlsx');
      formData.append('order_number', orderNumber);
      formData.append('original_file_url', fileUrl);

      const response = await axios.post(`${API_BASE_URL}/api/orders/update-furniture/`, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
          'Authorization': `Token ${localStorage.getItem('token')}`
        }
      });

      if (response.data.success) {
        console.log('✅ Файл успешно обновлен');
        setError(null);
      } else {
        throw new Error(response.data.message || 'Ошибка сохранения');
      }
    } catch (err) {
      console.error('❌ Ошибка сохранения файла:', err);
      setError(err.response?.data?.message || 'Не удалось сохранить изменения');
    } finally {
      setSaving(false);
    }
  };

  // Сохраняем изменения при внешнем вызове (если нужно)
  useImperativeHandle(ref, () => ({
    async saveChanges() {
      await saveUpdatedFile(furnitureItems);
    }
  }));

  // Обработка клика по элементу
  const handleTogglePurchased = async (itemId) => {
    if (saving) return;

    const updatedItems = furnitureItems.map(item =>
      item.id === itemId ? { ...item, purchased: !item.purchased } : item
    );

    setFurnitureItems(updatedItems);

    try {
      await saveUpdatedFile(updatedItems);
    } catch (e) {
      console.error('❌ Ошибка при автосохранении:', e);
    }
  };

  useEffect(() => {
    loadFurnitureFile();
  }, [fileUrl, loadFurnitureFile]);

  if (loading) return <div className="furniture-loading">Загрузка списка фурнитуры...</div>;
  if (error) return <div className="furniture-error">{error}</div>;

  return (
    <div className="furniture-list">
      <h3>Список фурнитуры</h3>
      {saving && <div className="furniture-saving">Сохранение изменений...</div>}
      {furnitureItems.length === 0 ? (
        <p>Список фурнитуры пуст</p>
      ) : (
        <div className="furniture-items">
          {furnitureItems.map(item => (
            <div
              key={item.id}
              className={`furniture-item ${item.purchased ? 'purchased' : ''} ${saving ? 'disabled' : ''}`}
              onClick={() => !saving && handleTogglePurchased(item.id)}
            >
              <div className="furniture-checkbox">
                {item.purchased ? '✓' : ''}
              </div>
              <div className="furniture-details">
                <div className="furniture-name">{item.name}</div>
                <div className="furniture-quantity">Количество: {item.quantity}</div>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
});

export default FurnitureList;