import { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import axios from "axios";
import "./OrderDetails.css"; // Подключаем стили
import API_BASE_URL from "../config"; // Импортируем API URL
import AddressLink from "./AddressLink";
import FurnitureList from "../components/FurnitureList";


// 🔧 ВСТАВЬ ПЕРЕД компонентом OrderDetails

const getSecureFileUrl = (path) => {
  if (!path) return "#";

  if (path.startsWith("http://")) {
    return path.replace("http://", "https://");
  }

  if (path.startsWith("https://")) {
    return path;
  }

  return `${window.location.origin}${path}`;
};

const OrderDetails = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [order, setOrder] = useState(null);
  const [loading, setLoading] = useState(true);
  const [downloadedFiles, setDownloadedFiles] = useState(new Set()); // Храним скачанные файлы
  const [selectedImage, setSelectedImage] = useState(null);
  const [currentImageIndex, setCurrentImageIndex] = useState(0);
  const [showFurniture, setShowFurniture] = useState(false);
  const [furnitureFile, setFurnitureFile] = useState(null);

  const statusLabels = {
    new: "Новый",
    in_progress: "В работе",
    waiting: "Ожидает предоплаты",
    agreed: "Согласован",
    completed: "Завершён",
    cancelled: "Отменён",
  };


  const handleEditClick = () => {
    navigate(`/orders/edit/${order.id}`, {
      state: { orderNumber: order.order_number },
    });
  };


  const formatPhone = (num) => {
    if (!num) return "";
  
    // Удаляем всё кроме цифр
    const digits = num.replace(/\D/g, "");
  
    // Приводим к формату 11-значного номера с +7
    const clean = digits.length === 11 && digits.startsWith("8")
      ? "7" + digits.slice(1)
      : digits;
  
    // Форматируем как +7 (999) 123-45-67
    return clean.replace(/(\d{1})(\d{3})(\d{3})(\d{2})(\d{2})/, "+$1 ($2) $3-$4-$5");
  };


  useEffect(() => {
    const fetchOrderDetails = async () => {
      try {
        const token = localStorage.getItem("token");
        if (!token) {
          navigate("/auth");
          return;
        }

        console.log(`📡 Запрос деталей заказа ID: ${id}`);
        const response = await axios.get(`${API_BASE_URL}/api/orders/${id}/`, {
          headers: { Authorization: `Token ${token}` },
        });
        console.log("✅ Детали заказа загружены:", response.data);
        setOrder(response.data);
      } catch (error) {
        console.error("❌ Ошибка загрузки заказа:", error.response?.data || error);
        navigate("/dashboard");
      } finally {
        setLoading(false);
      }
    };

    fetchOrderDetails();
  }, [id, navigate]);

  useEffect(() => {
    if (order?.attachments) {
      // Ищем файл фурнитуры среди вложений
      const furnitureAttachment = order.attachments.find(file => {
        const fileName = decodeURIComponent(file.file.split("/").pop());
        return fileName.toLowerCase().includes('фурнитура') && fileName.endsWith('.xls');
      });
      
      if (furnitureAttachment) {
        const fileUrl = getSecureFileUrl(furnitureAttachment.file);

        setFurnitureFile(fileUrl);
      }
    }
  }, [order]);

  if (loading) {
    return <p>Загрузка данных...</p>;
  }

  if (!order) {
    return <p>Ошибка: заказ не найден.</p>;
  }

  // Функция скачивания файла и изменения цвета ссылки
  const handleDownload = async (fileUrl, fileName) => {
    console.log(`📥 Попытка скачать файл: ${fileUrl} -> ${fileName}`);
    try {
      const response = await axios.get(fileUrl, { responseType: "blob" });
      console.log("✅ Файл успешно загружен:", response);
      const blob = new Blob([response.data], { type: response.headers["content-type"] });
      const link = document.createElement("a");
      link.href = window.URL.createObjectURL(blob);
      link.setAttribute("download", fileName);
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      console.log("📂 Файл загружен и сохранён:", fileName);
      setDownloadedFiles((prev) => new Set(prev).add(fileName));
    } catch (error) {
      console.error("❌ Ошибка скачивания файла:", error);
      alert("Ошибка скачивания файла. Проверьте консоль.");
    }
  };

  // Функция для определения категории файла по имени или пути
  const getFileCategory = (fileName) => {
    const lowerFileName = fileName.toLowerCase();
    
    // Эскизы: изображения и файлы дизайна
    if (lowerFileName.match(/\.(jpg|jpeg|png|gif|bmp|svg|psd|ai|sketch)$/)) {
      return 'sketches';
    }
    
    // Файлы для производства: 3D модели, чертежи и технические файлы
    if (lowerFileName.match(/\.(stl|obj|step|stp|dwg|dxf|iges|igs|x_t|sldprt|sldasm|b3d)$/)) {
      return 'production';
    }
    
    // Файлы для заказа: только Excel файлы
    if (lowerFileName.match(/\.(xls|xlsx|xlsm|xlsb|xlt|xltx|xltm)$/)) {
      return 'order';
    }
    
    // Прочие файлы
    return 'other';
  };

  // Группировка файлов по категориям
  const groupFilesByCategory = (files) => {
    return files.reduce((acc, file) => {
      const fileName = decodeURIComponent(file.file.split("/").pop());
      const category = getFileCategory(fileName);
      if (!acc[category]) {
        acc[category] = [];
      }
      acc[category].push(file);
      return acc;
    }, {
      sketches: [],
      production: [],
      order: [],
      other: []
    });
  };

  // Функция для открытия изображения в модальном окне
  const handleImageClick = (imageUrl, index) => {
    setSelectedImage(imageUrl);
    setCurrentImageIndex(index);
  };

  // Функция для перехода к следующему изображению
  const handleNextImage = (e) => {
    e.stopPropagation();
    const sketches = order.attachments.filter(file => {
      const fileName = decodeURIComponent(file.file.split("/").pop()).toLowerCase();
      return fileName.match(/\.(jpg|jpeg|png|gif|bmp|svg)$/);
    });
    const nextIndex = (currentImageIndex + 1) % sketches.length;
    const nextFile = sketches[nextIndex];
    const nextUrl = nextFile.file.startsWith("http")
      ? nextFile.file
      : `${API_BASE_URL}${nextFile.file}`;
    setSelectedImage(nextUrl);
    setCurrentImageIndex(nextIndex);
  };

  // Функция для перехода к предыдущему изображению
  const handlePrevImage = (e) => {
    e.stopPropagation();
    const sketches = order.attachments.filter(file => {
      const fileName = decodeURIComponent(file.file.split("/").pop()).toLowerCase();
      return fileName.match(/\.(jpg|jpeg|png|gif|bmp|svg)$/);
    });
    const prevIndex = (currentImageIndex - 1 + sketches.length) % sketches.length;
    const prevFile = sketches[prevIndex];
    const prevUrl = prevFile.file.startsWith("http")
      ? prevFile.file
      : `${API_BASE_URL}${prevFile.file}`;
    setSelectedImage(prevUrl);
    setCurrentImageIndex(prevIndex);
  };

  return (
    <div className="order-details-container">
      <h2>Заказ №{order.order_number}</h2>
      <p>
        <strong>ФИО:</strong> {order.full_name || "Не указано"}
      </p>
      <p>
  <strong>Телефон:</strong>{" "}
  {order.phone_number ? (
    <a
      href={`tel:${order.phone_number}`}
      style={{
        color: "#007bff",
        textDecoration: "none",
        fontWeight: "500",
        display: "inline-flex",
        alignItems: "center",
        gap: "6px",
      }}
    >
      📞 {formatPhone(order.phone_number)}
    </a>
  ) : (
    "Не указан"
  )}
</p>
<p>
  <strong>Адрес:</strong>{" "}
  {order.address ? <AddressLink address={order.address} /> : "Не указано"}
</p>
      <p>
        <strong>Изделие:</strong> {order.product || "Не указано"}
      </p>
      <p>
        <strong>Цена:</strong> {order.total_price ? `${order.total_price} ₽` : "Не указано"}
      </p>
      <p>
        <strong>Предоплата:</strong> {order.prepayment ? `${order.prepayment} ₽` : "Не указано"}
      </p>
      <p>
        <strong>Дата приёма:</strong>{" "}
        {order.received_date ? new Date(order.received_date).toLocaleDateString() : "Не указано"}
      </p>
      <p>
        <strong>Дата готовности:</strong>{" "}
        {order.due_date ? new Date(order.due_date).toLocaleDateString() : "Не указано"}
      </p>
      <p>
        <strong>Создатель:</strong> {order.created_by_username || "Не указан"}
      </p>
      <p>
  <strong>Статус:</strong> {statusLabels[order.status] || "Неизвестно"}
</p>
      

      {/* Модальное окно для просмотра изображений */}
      {selectedImage && (
        <div className="image-modal" onClick={() => setSelectedImage(null)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <button className="close-modal" onClick={() => setSelectedImage(null)}>
              ✕
            </button>
            <button className="modal-nav modal-prev" onClick={handlePrevImage}>
              ‹
            </button>
            <img src={selectedImage} alt="Preview" className="modal-image" />
            <button className="modal-nav modal-next" onClick={handleNextImage}>
              ›
            </button>
          </div>
        </div>
      )}

      {/* Список загруженных файлов по категориям */}
      {order.attachments && order.attachments.length > 0 && (
        <div className="attachments">
          <strong>Файлы:</strong>
          
          {(() => {
            const groupedFiles = groupFilesByCategory(order.attachments);
            return (
              <>
                {/* Эскизы */}
                {groupedFiles.sketches.length > 0 && (
                  <div className="attachment-section">
                    <h3>📐 Эскизы и изображения</h3>
                    <div className="sketches-grid">
                      {groupedFiles.sketches.map((file, index) => {
                        const fileName = decodeURIComponent(file.file.split("/").pop());
                        const fileUrl = getSecureFileUrl(file.file);
                        return (
                          <div key={index} className="sketch-item">
                            <img
                              src={fileUrl}
                              alt={fileName}
                              className="sketch-preview"
                              onClick={() => handleImageClick(fileUrl, index)}
                            />
                            <div className="sketch-actions">
                              <button
                                className="sketch-download"
                                style={{ color: downloadedFiles.has(fileName) ? "gray" : "blue" }}
                                onClick={() => handleDownload(fileUrl, fileName)}
                              >
                                📂 Скачать
                              </button>
                            </div>
                          </div>
                        );
                      })}
                    </div>
                  </div>
                )}

                {/* Файлы для производства */}
                {groupedFiles.production.length > 0 && (
                  <div className="attachment-section">
                    <h3>🏭 Файлы для производства</h3>
                    <ul>
                      {groupedFiles.production.map((file, index) => {
                        const fileName = decodeURIComponent(file.file.split("/").pop());
                        const fileUrl = getSecureFileUrl(file.file);
                        return (
                          <li key={index}>
                            <button
                              className="download-button"
                              style={{ color: downloadedFiles.has(fileName) ? "gray" : "blue" }}
                              onClick={() => handleDownload(fileUrl, fileName)}
                            >
                              📂 Скачать {fileName}
                            </button>
                          </li>
                        );
                      })}
                    </ul>
                  </div>
                )}

                {/* Файлы для заказа */}
                {groupedFiles.order.length > 0 && (
                  <div className="attachment-section">
                    <h3>📋 Файлы заказа</h3>
                    <ul>
                      {groupedFiles.order.map((file, index) => {
                        const fileName = decodeURIComponent(file.file.split("/").pop());
                        const fileUrl = getSecureFileUrl(file.file);
                        const isFurnitureFile = fileName.toLowerCase().includes('фурнитура') && fileName.endsWith('.xls');
                        return (
                          <li key={index} className="file-item">
                            <div className="file-actions">
                              <button
                                className="download-button"
                                style={{ color: downloadedFiles.has(fileName) ? "gray" : "blue" }}
                                onClick={() => handleDownload(fileUrl, fileName)}
                              >
                                📂 Скачать {fileName}
                              </button>
                              {isFurnitureFile && (
                                <button
                                  className="furniture-button"
                                  onClick={() => setShowFurniture(!showFurniture)}
                                >
                                  {showFurniture ? "❌ Скрыть список" : "📋 Открыть список"}
                                </button>
                              )}
                            </div>
                            {isFurnitureFile && showFurniture && (
                              <div className="furniture-container">
                                <FurnitureList
                                  orderNumber={order.order_number}
                                  fileUrl={fileUrl}
                                />
                              </div>
                            )}
                          </li>
                        );
                      })}
                    </ul>
                  </div>
                )}

                {/* Прочие файлы */}
                {groupedFiles.other.length > 0 && (
                  <div className="attachment-section">
                    <h3>📎 Прочие файлы</h3>
                    <ul>
                      {groupedFiles.other.map((file, index) => {
                        const fileName = decodeURIComponent(file.file.split("/").pop());
                        const fileUrl = getSecureFileUrl(file.file);
                        return (
                          <li key={index}>
                            <button
                              className="download-button"
                              style={{ color: downloadedFiles.has(fileName) ? "gray" : "blue" }}
                              onClick={() => handleDownload(fileUrl, fileName)}
                            >
                              📂 Скачать {fileName}
                            </button>
                          </li>
                        );
                      })}
                    </ul>
                  </div>
                )}
              </>
            );
          })()}
        </div>
      )}

      
      {/* Контейнер для кнопок */}
      <div className="button-container">
        <button
          className="back-button"
          onClick={() => navigate("/dashboard")}
        >
          Назад
        </button>
        <button
  className="edit-order-button"
  onClick={handleEditClick}
>
  Редактировать заказ
</button>
      </div>
    </div>
  );
};

export default OrderDetails;