import React from "react";
import AppRoutes from "./routes";
import { BrowserRouter } from "react-router-dom";
import { YMInitializer } from 'react-yandex-metrika';

// Включаем флаг v7_startTransition
const router = {
  future: {
    v7_startTransition: true
  }
};

const App = () => {
  return (
    <BrowserRouter {...router}>
      <YMInitializer
        accounts={[100811410]}
        options={{
          clickmap: true,
          trackLinks: true,
          accurateTrackBounce: true,
          webvisor: true,
          trackHash: true,
        }}
        version="2"
      />
      <AppRoutes />
    </BrowserRouter>
  );
};

console.log("🌍 ACTIVE ENV:", process.env.REACT_APP_TEST_ENV);

export default App;
